import React from 'react';
import {useFeatureServices} from './use-feature-services';

export function App(): JSX.Element {
  const {'s2:logger': logger = console} = useFeatureServices();

  React.useEffect(() => logger.debug('Hello World!'), [logger]);

  return (
    <p>
      <strong>VGS SE</strong> Starter Feature App
    </p>
  );
}
